@import 'variables';

.about {
    min-width: 300px;
    margin: 0;
}

.name {
    font-size: 5vw;
    font-weight: 200;
    text-transform: uppercase;
    letter-spacing: .7rem;
    color: black;

}

.foto {
    width: 60vw;
    height: auto;
    object-fit: contain;
    opacity: 0.2;

}
.foto_div {
    position: absolute;
    top: 150px;
    left: 40%;
    margin: 30px 50px;
}

.stack {
    font-size: 1rem;
    font-weight: 500;
    color: black;
}

.left {
    min-height: 70vh;
    display: flex;
    justify-content: left;
    align-items: center;
    flex-flow: row wrap;
}





