@import 'variables';
.card_widget {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 5px 5px;
  padding: 20px;
  background-color: rgba(0, 85, 255, 0.05);
  border-radius: 9px;
  //min-width: 120px;
  //width: 49%;
  //height: 35vh;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
@media (min-width: 1080px) {
  .card_widget {
    width: 49%;
  }
}
@media (max-width: 1080px) {
  .card_widget {
    width: 100%;
    min-width: 280px;
  }
}
.card_type {
  font-size: 0.7em;
  color: black;
  font-weight: bold;
  background-color: $background-color;
  border-radius: 0px 5px;
  padding: 2px 5px;
  position: relative;
  top: -2px;
}
.card_status {
  font-size: 1em;
  padding: 0px 5px;
  float: right;
}
.card_head {
}
.card_name {
  font-size: 1.5em;
  color: black;
  text-align: center;
  padding: 10px;
}
.card_description {
  font-size: 1em;
  color: black;
  text-align: center;
}
.card_links {
  margin: 10px 0;
  height: 50px;
  color: $link-color;
}
.card_links a:link {
  text-decoration: none;
  color: $link-color;
  transition: 0.3s;
}
.card_links a:hover {
  font-size: 1.05em;
}
.card_links a:visited {
  color: $link-color;
}
.card_footer {

}
.card_stack {
  font-size: 0.8em;
}
.card_foto {
  min-width: 120px;
  width: 100%;
  height: auto;
  object-fit: contain;
  opacity: 0.5;
  border-radius: 9px;


}
.card_foto_div {
  position: absolute;

  border-radius: 9px;
  min-width: 120px;
  width: 100%;
  //position: absolute;
  //top: 80px;
  //left: 120px;
  //margin: 30px 50px;
}